* {
    padding: 0px;
    margin: 0px;
    border: 0px;
    scroll-behavior: smooth;
}

*,
*:before,
*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

aside,
nav,
footer,
header,
section {
    display: block;
}

html,
body {
    height: 100%;
    min-width: 320px;
}

body {
    line-height: 1;
    font-family: "MazzardM";
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
    font-family: "MazzardM";
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}

body {
    color: #000;
    font-size: 14px;
}

body.lock {
    overflow: hidden;
    position: relative;
    min-height: 100%;
}

body.lock::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.6980392157);
    z-index: 150;
}

.product-select {
    overflow: hidden;
    position: relative;
    min-height: 100%;
}

.product-select::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1000%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(2px);
    z-index: 150;
}

.wrapper {
    width: 100%;
    min-height: 100%;
    overflow: hidden;
    position: relative;
}

.wrapper::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #051223;
}

.wrapper::after {
    position: absolute;
    content: "";
    width: 35.7%;
    padding: 0 0 35.7% 0;
    border-radius: 50%;
    top: 196px;
    left: 110px;
    background: rgba(0, 55, 128, 0.8);
    filter: blur(350px);
    z-index: 20;
}

