.link-donaciones {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    font-size: 0;
    align-items: center;
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.4);
    width: 100%;
    max-width: 575px;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1));
    padding: 20px;
}

@media (max-width: 1200px) {
    .link-donaciones {
        padding: 17.5px;
    }
}

@media (max-width: 479.98px) {
    .link-donaciones {
        padding: 15px;
    }
}

.link-donaciones__image {
    flex: 0 0 112px;
    width: 112px;
    height: 112px;
    border-radius: 15px;
    overflow: hidden;
    margin: 0 30px 0 0;
}

.link-donaciones__image img {
    max-width: 100%;
}

