main {
    min-height: 100vh;
    right: 0;
    width: 100%;
}

.fade-enter-active,
.fade-enter {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 0;
}

.fade-exit {
    opacity: 0;
    transition: all .3s ease;
}

.fade-enter-done {
    opacity: 1;
    transition: all .3s ease;
}

.game-users {
    margin-left: 0;
}

.users__name {
    margin-left: 20px;
}

